@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap');

.centered {
  justify-content: center; /* center items horizontally, in this case */
  align-items: center;     /* center items vertically, in this case */
  display: flex;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

a{
  color: white;
}

.sun {
  display:flex;
  justify-content: center; /* center items horizontally, in this case */
  align-items: center;     /* center items vertically, in this case */
  border-radius:50%;
  height:200px;
  width:200px;
  right: 100px;
  background:orange;
  box-shadow: 0 0 10px orange,
                0 0 60px orange,
                0 0 200px yellow,
                inset 0 0 80px yellow;
  z-index:12;
  z-index:-99;
}

body {
  font-family: 'Poppins', sans-serif;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  min-height: 0vh;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: 450vh 200vh;
}

.container {
  max-width: 500px;
  margin: 30px auto;
  margin-bottom: 10px;
  overflow: auto;
  min-height: 300px;
  border: 1px solid forestgreen;
  padding: 10px;
  border-radius: 5px;
  background-color: limegreen;
}


.header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  margin-left: 40px;
  margin-right: 40px;
}

.task {
  background: #f4f4f431;
  margin: 5px;
  padding: 10px 20px;
  cursor: pointer;
}

.task_reminder
{
  background: #f4f4f431;
  margin: 5px;
  padding: 10px 20px;
  cursor: pointer;
  border-left: 5px solid green;
}

.task_dd {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.btn {
  display: inline-block;
  background: #000;
  color: #fff;
  border: none;
  padding: 10px 20px;
  margin: 5px;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  font-size: 15px;
  font-family: inherit;
}

.delete_icon{
  color :"red";
  cursor: 'pointer'
}

.btn:focus {
  outline: none;
}

.btn:active {
  transform: scale(0.98);
}

.btn-block {
  display: block;
  width: 100%;
}

.add-form {
  margin-bottom: 40px;
}

.form-control {
  margin: 20px 0;
}

.form-control label {
  display: block;
}

.form-control input {
  width: 100%;
  height: 40px;
  margin: 5px;
  padding: 3px 7px;
  font-size: 17px;
}

.form-control-check {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.form-control-check label {
  flex: 1;
}

.form-control-check input {
  flex: 2;
  height: 20px;
}

.background-fullviewport{
  right: -50vh;
  width: 100vh;
  padding: 20px;
  height: 0;

}

footer {
  margin-top: 30px;
  text-align: center;
}
